import React,{useState, useEffect, useRef} from 'react'
import './Manage.css'
import axios from './axios.js'
import Row from './Row.js'
import Button from '@material-ui/core/Button';
import { Switch, Route, Link, Redirect }  from "react-router-dom";
import Table from '@material-ui/core/Table';

function Manage() {
	const [details,setDetails]=useState([])
	
	useEffect(() => {
		async function fetchURL() {
			const response=await axios.put('/v2/manage',{id:localStorage.getItem('active_user')});
			setDetails(response.data)
			return response;
		}
		fetchURL();
	}, [details])
	
	const callback=(i)=>{
		let d=details
		d.splice(i,1)
		setDetails(d)
	}
	return (
		<div className="main">
		<div className="main_but">
			<Button onClick={()=>{window.location.href='/'}}variant="outlined" className="but_but"> Home </Button>
	        <Link to='/add'>
	            <Button variant="outlined" className="but_but"> Add </Button>
	        </Link>
	    </div>
			<Table border="1" cellPadding="5" cellSpacing="1" className="main_table">
				<tr>
					<td>S.No</td>
					<td>Description</td>
					<td>Song</td>
					<td>URL</td>
					<td>Edit</td>
					<td>Delete</td>
				</tr>
				{details.map((val,ind) => (
					<tr>
						<Row cb={callback} oid={val._id} ind={ind} desc={val.description} song={val.song} url={val.url} />
				    </tr>
				))}
			</Table>
		</div>
	)
}

export default Manage