import React,{useRef,useState,useEffect} from 'react'
import './Row.css'
import axios from './axios.js'
import { Input,Button,InputLabel,TextareaAutosize,TextField } from '@material-ui/core';

function Row({desc,url,song,ind,oid,cb}) {
	const dref=useRef(null)
	const sref=useRef(null)
	const uref=useRef(null)

	const [echeck,setEcheck]=useState(true)

	const changeEdit = () => {
		dref.current.disabled=false;
		sref.current.disabled=false;
		uref.current.disabled=false;
		setEcheck(false)
	}

	const changeUpdate = () => {
		dref.current.disabled=true;
		sref.current.disabled=true;
		uref.current.disabled=true;
		setEcheck(true);
		const data={
			id: oid,
			user: localStorage.getItem('active_user'),
			url : uref.current.value,
			description	: dref.current.value,
			song :  sref.current.value
		}
		async function updateAPI () {
			const response=await axios.post('/v2/manage',{dat:data})
			return response;
		}
		updateAPI()
	}

	const del = () => {
		console.log(oid);
		async function deleteAPI () {
			const response=await axios.put('/v2/managedel',{id: oid})
			return response;
		}
		deleteAPI()
		cb(ind);
	}
	return (
		<>
			<td>{ind}</td>
			<td><TextareaAutosize className="area" rowsMin={3} ref={dref} disabled>{desc}</TextareaAutosize></td>
			<td><TextareaAutosize className="area" rowsMin={3} ref={sref} disabled>{song}</TextareaAutosize></td>
			<td><input className="inp" ref={uref} value={url} disabled/></td>
			{echeck ? (<td><Button  className="btn" variant="contained" onClick={changeEdit}>Edit</Button></td>) : (<td><Button className="btn"  variant="contained" onClick={changeUpdate}>Update</Button></td>)}
			<td><Button className="btn" variant="contained" onClick={del}> Delete</Button></td>	
		</>
	)
}

export default Row