import React,{useState, useEffect} from "react";
import './VideoSidebar.css';
import axios from './axios';
import FavoriteIcon from "@material-ui/icons/Favorite";
import MessageIcon from "@material-ui/icons/Message";
import ShareIcon from "@material-ui/icons/Share";
import FavoriteBorderIcon from "@material-ui/icons/FavoriteBorder";
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';

function VideoSidebar({objid,likes,comments,url}) {
  const [liked,setLiked] = useState();

  useEffect(() => {
      async function fetchAPI() {
        const response = await axios.put("/v2/checklike",{objid: objid, user: localStorage.getItem('active_user')}).then((res) => {
          if(res.data == true){
            setLiked(true);
          } else {
            setLiked(false);  
          }
        }).catch((err)=>{
          console.log(err);
        });
        return response;
      }
      fetchAPI();
    }, [])

  async function likeOff () {
    setLiked(false);
    await axios.put('/v2/posts/likeoff',{objid: objid,user: localStorage.getItem('active_user')}).then(() => {
      console.log('done');
    }).catch((err) => {
        console.log(err);
    })
  }

  async function likeOn () {
    setLiked(true);
    axios.put('/v2/posts/likeon',{objid: objid,user: localStorage.getItem('active_user')}).then(() => {
      console.log('done 2');
    }).catch((err) => {
      console.log(err);
    })
  }

  return (
    <div className="videoSidebar" style={{cursor:"pointer"}}>
        <div className="videoSidebar_button">
        {liked ? (
            <FavoriteIcon fontSize="large" onClick={likeOff} />
          ) : (
            <FavoriteBorderIcon fontSize="large" onClick={likeOn} />
          )}
          <p>{liked ? likes : likes}</p>
        </div>

        <div className="videoSidebar_button">
          <MessageIcon fontSize="large" />
          <p>{comments}</p>
        </div>

        <div className="videoSidebar_button">
        <a href={url} download="file">
          <CloudDownloadIcon fontSize="large" />
        </a>
        </div>
    </div>
  );
}

export default VideoSidebar;
